@import url("https://fonts.googleapis.com/css2?family=Caprasimo&display=swap");

body {
  scroll-behavior: smooth;
  background-color: black;
}

.App {
  text-align: center;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: black;
  min-height: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  text-decoration: none;
  color: white;
  font-family: "Caprasimo", cursive;
}

.Menubar {
  position: sticky;
}

.Divctaanimation {
  width: 100%;
  height: 570px;
  animation: Divctaanimation 10s ease-out 0s infinite normal;
  transition-timing-function: ease-in-out;
  background-size: cover;
  background-position: 100% 20%;
}

@keyframes Divctaanimation {
  0% {
    background-image: url("../public/h.jpg");
  }
  16.66% {
    background-image: url("../public/cinco.jpg");
  }
  33% {
    background-image: url("../public/dois.jpg");
  }
  64% {
    background-image: url("../public/tres.jpg");
  }
  80% {
    background-image: url("../public/quatro.jpg");
  }
  100% {
    background-image: url("../public/hum.jpg");
  }
}

.Divtxtdev {
  width: 100%;
  height: 300px;
  background-color: black;
}

.TxtDev {
  text-align: center;
  font-size: 9vh;
  font-family: "Caprasimo", cursive;
  margin-top: 0px;
  background-color: #ffffff;
  z-index: 1;
}

.videoTag {
  width: 100%;
  height: 600px;
  margin-top: 100px;
}

.Cta_hum {
  margin-top: 480px;
  width: 200px;
  height: 40px;
  border: 2px solid white;
  color: white;
  font-size: 0.9em;
  background-color: transparent;
  font-family: "Caprasimo", cursive;
  cursor: pointer;
}

.Cta_hum:hover {
  border: 3px solid yellow;
  color: yellow;
  transform: 1s;
  animation: alternate;
}

.Slogan {
  font-family: "Caprasimo", cursive;
  position: absolute;
  font-size: 10vh;
  color: white;
}

.Papresentacao {
  width: 80%;
  text-align: left;
  margin-left: 120px;
  margin-top: 10%;
  font-size: 2vh;
}

.Divslidesjobs {
  width: 100%;
  height: 550px;
  background-color: blueviolet;
}

.Divform {
  width: 100%;
  height: 550px;
  background-color: orange;
}

.Divanimacao {
  width: 100%;
  height: 450px;
  background-size: 50% 80%;
  background-attachment: scroll;
  background-repeat: no-repeat;
  margin-top: 10px;
  text-align: center;
  animation: Divanimacao 10s ease-out 0s infinite normal;
  transition-timing-function: steps(6, end);
  background-position: 100% 20%;
}

@keyframes Divanimacao {
  0% {
    background-image: url("../public/s.jpg");
  }
  25% {
    background-image: url("../public/k.jpg");
  }
  50% {
    background-image: url("../public/a.jpg");
  }
  75% {
    background-image: url("../public/t.jpg");
  }
  100% {
    background-image: url("../public/e.jpg");
  }
}

.Txtolympic {
  position: absolute;
  color: white;
  font-family: "Roboto", sans-serif;
  text-align: left;
  width: 500px;
  margin-left: 50px;
  margin-top: 60px;
}

.Txtpunk {
  font-size: 70px;
  font-family: "Caprasimo", cursive;
  color: white;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-left: 25px;
}

.Divfooter {
  width: 100%;
  height: 225px;
  margin-top: 0px;
  background-image: url("../public/art.jpg");
  background-position: 100% 18%;
}

.Divfooterdois {
  width: 100%;
  height: 370px;
  margin-top: 65px;
  background-color: #ffffff;
  background-position: 50% 25%;
}

.Divfootertres {
  width: 100%;
  height: 940px;
  margin-top: 65px;
  background-image: url("../public/bgpunk.jpg");
  background-size: cover;
  background-position: 100% 20%;
}

.Videoskt {
  width: 100%;
  height: 5px;
  margin-top: 65px;
  background-color: #000000;
  background-size: cover;
  background-position: 100% 20%;
}

.Imgchorao {
  width: 15%;
  display: none;
}

.Botaomeio {
  background-color: transparent;
  border: 2px solid #ffffff;
  color: #ffffff;
  font-family: "Caprasimo", cursive;
  font-size: 1.2em;
  width: 240px;
  height: 50px;
  cursor: pointer;
}

.Botaomeio:hover {
  border: 3px solid yellow;
  color: yellow;
  transform: 1s;
  animation: alternate;
}

.Botaomeiodois {
  background-color: transparent;
  border: 2px solid #ffffff;
  color: #ffffff;
  font-family: "Caprasimo", cursive;
  font-size: 1.2em;
  width: 240px;
  height: 50px;
  cursor: pointer;
  margin-top: 60px;
}

.Logotxt {
  float: left;
  position: absolute;
}

.Logogrowimg {
  width: 20% !important;
}

.Endereco {
  color: #ffffff !important;
  font-family: "Roboto Extra", sans-serif;
}

.flex-container {
  display: flex;
}

.flex-container > div {
  flex-direction: wrap;
  border: 1px solid #000000;
  width: 200px;
  height: 200px;
  margin: 20px;
  padding: 20px;
  margin-left: 55px;
  margin-top: 60px;
}

.repository-list {
  background-color: gray;
  list-style: none !important;
  text-align: left;
}

a {
  list-style: none;
}
